.card {
    position: relative;
    background-color: white;
    width: 600px;
    padding: 20px;
    max-width: 85vw;
    height: 80vh;
    background-repeat: no-repeat;
    box-shadow: 0px 10px 53px 0px rgba(0, 0, 0, 0.3);
    border-radius: 20px;
    background-size: cover;
    background-position: center;
}

.tinderCards {
    width: 100%;
    min-height: 100vh;
}

.tinderCards h2 {
    text-transform: uppercase;
    padding-left: 30vw;
    padding-top: 2px;
    font-size: 2vw;
}

.tinderCards__cardContainer {
    display: flex;
    justify-content: center;
    margin-top: 10vh;
}

.swipe {
    position: absolute;
}

.cardContent {
    width: 100%;
    height: 100%;
}

.card h3 {
    position: absolute;
    bottom: 0;
    margin: 10px;
    color: white;
}